import styles from "./Transaction2.module.css";
import bg1 from "../assets/Component.png";
import { Swiper, SwiperSlide } from "swiper/react";
import "swiper/swiper.min.css";
import "swiper/components/pagination/pagination.min.css";
import SwiperCore, { Pagination ,Mousewhee,Autoplay} from "swiper/core";
import {groupData} from "../utils/constants/groupData.js"
import { useEffect,useState } from "react";
import {hasGroupStopped,getUniqueTokenNames,getTokenPrice,getVolume24h,getTotalUsers,getPriceChangePercentage } from "../utils/contractData.js";
import icon1 from "../assets/image_16.png";
import icon2 from "../assets/image_14.png";
import icon3 from "../assets/image_15.png";
import icon4 from "../assets/image_17.png";
import icon5 from "../assets/image_18.png";
import icon6 from "../assets/image_19.png";
import icon7 from "../assets/cake.png";
import icon8 from "../assets/coin2.png";
import classnames from "classnames";
import { useSelector, useDispatch} from "react-redux";
import up from "../assets/Up.png"
import down from "../assets/Down.png"

function Transaction2(){
    const [rowNum,setRowNum]=useState(0)
    const dispatch = useDispatch();
  const [vol24,setVol24]=useState(["","","","","","","","","","","","","","","","","",""]);
  const [totalUsers,setTotalUsers]=useState(["","","","","","","","","","","","","","","","","",""]);
  const [trend,setTrend]=useState(["","","","","","","","","","","","","","","","","",""]);
  const [asset,setAsset]=useState(["","","","","","","","","","","","","","","","","",""]);
  const [live,setLive]=useState(["","","","","","","","","","","","","","","","","",""]);
  

  useEffect(() => {
    async function f1(){
var v=[];
for (const property in groupData) {
  
  var v24=await getVolume24h(property)
 v.push(v24.volume);
   }
setVol24(v);
    }
    async function f2(){
     
var u=[];
for (const property in groupData) {
var statistic =await getTotalUsers(property);

u.push(statistic)
}
setTotalUsers(u);
    }
    async function f3(){

var t=[];
for (const property in groupData) {
var statistic =await getPriceChangePercentage(property);

if(statistic>0)
t.push("UP");
else
t.push("Down");
}
setTrend(t);
    
    }
    setRowNum(Object.keys(groupData).length);
    var a=[];
    for (const property in groupData) {
      
      a.push(groupData[property].token)
    }
    async function f4(){
     
        var l=[];
        for (const property in groupData) {
        var paused =await hasGroupStopped(property);
       
        if(paused==true)
        l.push("NOT LIVE")
        else
        l.push("LIVE")
        }
        setLive(l);
            } 
    setAsset(a)
    dispatch({type:"tokensChange", tokens:a});
  f1();
   f2();
   f3();
   f4();
  },[]);
  
  var rows=[];
  for(var i=0;i<rowNum;i++)
  {
    rows.push(<TableRow key={i} asset={asset[i]} users={totalUsers[i]} volume={vol24[i]} trend={trend[i]} live={live[i]} index={i}/>);
  }

    return(
    <div>
       <div className={styles.box1}>
           <div className={styles.row}>
               <div className={styles.col1}>
                   <div className={styles.detailed}>Detailed Stats</div>
                   <div><Slider/></div>

               </div>
               <div className={styles.col2}>
<img src={bg1} className={styles.image1}></img>
               </div>
           </div>
           <div>
             <table className={styles.table}>
             <thead>
                 <tr>
                     <th className={classnames(styles.th,styles.th1)}>Asset</th>
                     <th className={classnames(styles.th,styles.th11)}></th>
                     <th className={classnames(styles.th,styles.th2)}>Volume 24h</th>
                     <th className={classnames(styles.th,styles.th3)}>Users</th>
                     <th className={classnames(styles.th,styles.th4)}>Trend</th>
                     <th className={classnames(styles.th,styles.th5)}>Live</th>
                 </tr>
              

             </thead> <tbody>  {rows}</tbody>
           </table>  
           </div>
       </div>
    </div>
    );
}
function Slider(){
const [groups,setGroups]=useState([]);
const [price,setPrice]=useState([]);
SwiperCore.use([Autoplay]);

useEffect(()=>{
    getUniqueTokenNames().then((res)=>{
        for (const property in res) {
    groups.push( <SwiperSlide key={property}>
   <SliderPannel key={property+"slide"} token={res[property]}/>
   </SwiperSlide>)
   
}
    })

},[])
let [width, setWidth] = useState(document.documentElement.clientWidth);

useEffect(() => {
  const resizeListener = () => {
    setWidth(document.documentElement.clientWidth)
  };
  window.addEventListener('resize', resizeListener);
  return () => {
    window.removeEventListener('resize', resizeListener);
  }
}, []);
var num=2;
if(width<=390)
    {
      num=1;
    }
    
   
const [swiper, setSwiper] = useState(null);

  const params = {  
    container: ".swiper-container",
    mousewheel: true, 
  };
   return(<div className={styles.slider}>
      <Swiper  {...params}
      slidesPerView={num}
      onSwiper={setSwiper}
      spaceBetween={20}
      initialSlide={0}  
      className={styles.swiper}
      allowPageScroll= {"vertical"}
      allowTouchMove= {false}
      mousewheel={{  
        forceToAxis: true,
       }}
     autoplay={{
        "delay": 2500,
        "disableOnInteraction": false,
      }}
      
      loop={true}
  >  
 {groups}

    </Swiper>
       
       </div>); 
}

function SliderPannel(props){
    const [price,setPrice]=useState(0);
    const [currentIcon,setCurrentIcon] = useState(null);
    useEffect(()=>{
        getTokenPrice(props.token).then((res)=>{
            setPrice(res);
        })
     /* const interval = setInterval(() => {
        getTokenPrice(props.token).then((res)=>{
            setPrice(res);
        })
      }, 60000);*/
      
      if(props.token.toUpperCase()=="BNB")
  setCurrentIcon(icon1) 
  else 
  if(props.token.toUpperCase()=="BTC")
  setCurrentIcon(icon2)
  else if(props.token.toUpperCase()=="ETH")
  setCurrentIcon(icon3)
  else if(props.token.toUpperCase()=="APPLE"||props.token.toUpperCase()=="APL")
  setCurrentIcon(icon4)
  else if(props.token.toUpperCase()=="GOOGLE"||props.token.toUpperCase()=="GOOGL")
  setCurrentIcon(icon5)
  else if(props.token.toUpperCase()=="TESLA"||props.token.toUpperCase()=="TSL"||props.token.toUpperCase()=="TSLA")
  setCurrentIcon(icon6)
  else if(props.token.toUpperCase()=="CAKE")
  setCurrentIcon(icon7)
  else
  setCurrentIcon(icon8)
  //return () => clearInterval(interval);  
    },[])
    var today = new Date();

    var date = today.toLocaleString('default', { month: 'short' })+' '+today.getDay()+' '+today.getFullYear()
    return(
<div>
    <div className={styles.sliderRow}>
        <div className={styles.sliderCol1}><img src={currentIcon} className={styles.sliderIcon}/></div>
        <div className={styles.sliderCol2}>
            <div className={styles.price}>{price}</div>
            <div className={styles.date}>{date}</div>
        </div>
        </div>
    
    </div>
    )
}
function TableRow(props,state){
    var asseticon;
  if(props.asset.toUpperCase()=="BNB")
  asseticon=<img src={icon1} className={classnames(styles.icon1,styles.icon1_s)} ></img>
  else 
  if(props.asset.toUpperCase()=="BTC")
  asseticon=<img src={icon2} className={classnames(styles.icon1)}></img>
  else if(props.asset.toUpperCase()=="ETH")
  asseticon=<img src={icon3} className={classnames(styles.icon1,)}></img>
  else if(props.asset.toUpperCase()=="APPLE"||props.asset.toUpperCase()=="APL")
  asseticon=<img src={icon4} className={classnames(styles.icon1)}></img>
  else if(props.asset.toUpperCase()=="GOOGLE"||props.asset.toUpperCase()=="GOOGL")
  asseticon=<img src={icon5} className={classnames(styles.icon1,styles.icon1_s)}></img>
  else if(props.asset.toUpperCase()=="TESLA"||props.asset.toUpperCase()=="TSL"||props.asset.toUpperCase()=="TSLA")
  asseticon=<img src={icon6} className={classnames(styles.icon1)}></img>
  else if(props.asset.toUpperCase()=="CAKE")
  asseticon=<img src={icon7} className={classnames(styles.icon1)}></img>
  else
  asseticon=<img src={icon8} className={classnames(styles.icon1)}></img>
  var downcss,upcss,trendcolor,livecolor;
  if(props.trend=="UP")
{
    downcss=styles.hide
    upcss=null;
    trendcolor=styles.green;
}
else{
    downcss=null;
    upcss=styles.hide;
    trendcolor=styles.purple;
}
if(props.live=="LIVE")
{
livecolor=styles.green;
}
else{
    livecolor=styles.purple;
}

    return(
<tr>
                     
                     <td className={classnames(styles.td,styles.td1)}>{asseticon}</td>
                     <td className={classnames(styles.td,styles.td11)}>{props.asset}</td>
                     <td className={classnames(styles.td,styles.td2)}>{props.volume}</td>
                     <td className={classnames(styles.td,styles.td3)}>{props.users}</td>
                     <td className={classnames(styles.td,styles.td4,trendcolor)}>{props.trend}<img src={down} className={classnames(styles.down,downcss)}/><img src={up} className={classnames(styles.up,upcss)}/></td>
                     <td className={classnames(styles.td,styles.td5,livecolor)}>{props.live}</td>
                 </tr>
    )
}
export default Transaction2;
