import styles from "./Transaction3.module.css";
import { useEffect, useState } from "react";
import online from "../assets/live1.gif"
import offline from "../assets/offline.gif"
import classnames from "classnames";
import icon1 from "../assets/image_16.png";
import icon2 from "../assets/image_14.png";
import icon3 from "../assets/image_15.png";
import icon4 from "../assets/image_17.png";
import icon5 from "../assets/image_18.png";
import icon6 from "../assets/image_19.png";
import icon7 from "../assets/cake.png";
import icon8 from "../assets/coin2.png";
import { useSelector, useDispatch} from "react-redux";
import {hasGroupStopped,getGroupIntervalSeconds } from "../utils/contractData.js";
import image1 from "../assets/8311-loading.gif"

import {groupData} from "../utils/constants/groupData.js"

function Transaction3() {
    const [live,setLive]=useState(true);
    const [colorCss1, setColorCss1] = useState(styles.color1);
    const [colorCss2, setColorCss2] = useState(styles.color2);
    const [animCss1, setAnimCss1] = useState(null);
    const [animCss2, setAnimCss2] = useState(styles.hide);
    const [liveRows,setLiveRows]=useState([]);
    const [pausedRows,setPausedRows]=useState([]);
    const [loadingCss,setLoadingCss]=useState(styles.hide1);
useEffect(()=>{
    async function f1(){
        setLoadingCss(null)
        var l=[];var p=[];
        var i=0;
       
        for (const property in groupData) {
            
          var flag=await hasGroupStopped(property)
          if(flag==true)
         p.push( <TableRow key={"l"+property+i} token={property} asset={groupData[property].token} index={i}/>)
           
           else{
               l.push( <TableRow key={"p"+property+i} token={property} asset={groupData[property].token} index={i} />)
           }
           i++;
        }
        setLoadingCss(styles.hide1)
        setLiveRows(l);
        setPausedRows(p);
            }
    f1();
},[])
var rows=null;
if(live==true)
rows=liveRows;
else
rows=pausedRows;
var loading=<img src={image1} className={classnames(styles.loading,loadingCss)}></img>
    return (
        <div className={styles.box}>
            <div className={styles.markets}>Markets</div>
            <div className={styles.hr}></div>
            <div className={styles.parrent}>
                <button className={classnames(styles.button, styles.button1, colorCss1)} onClick={() => { setColorCss1(styles.color1); setColorCss2(styles.color2);setAnimCss1(null);setAnimCss2(styles.hide);setLive(true);}}>
                    <div className={styles.buttonRow}>
                        <div className={styles.buttonColumn}>
                            <div className={styles.live}>Live Markets</div><div className={styles.now}>Predict Now</div>
                        </div>
                        <div className={styles.buttonColumn}>
                            <img src={online} className={classnames(styles.buttonAnim,animCss1)}></img>
                        </div>
                    </div>
                </button>
                <button className={classnames(styles.button, styles.button2, colorCss2)} onClick={() => { setColorCss1(styles.color2); setColorCss2(styles.color1);setAnimCss1(styles.hide);setAnimCss2(null); setLive(false);}}>
                <div className={styles.buttonRow}>
                        <div className={styles.buttonColumn}>
                            <div className={styles.live}>Offline Markets</div><div className={styles.now}>Come back later</div>
                        </div>
                        <div className={styles.buttonColumn}>
                            <img src={offline} className={classnames(styles.buttonAnim,animCss2)}></img>
                        </div>
                    </div></button></div>
                    {loading}
            <table className={styles.table}>
              
                <tbody>
             {rows}</tbody>
            </table>
        </div>
    )
}
function TableRow(props,state){
    const dispatch = useDispatch();
    const [currentIcon,setCurrentIcon] = useState(null);
    const [first,setfirst] = useState(null);
    const [duration,setDuration]=useState(0);
    const [durationText,setDurationText]=useState("");
    var buttonColor=styles.buttonColor1;
    var buttonText="Select"
    const currentGroup = useSelector(state => state.currentGroup);
    if(currentGroup==props.index)
    {
        buttonColor=styles.buttonColor2;
        buttonText="Selected"
    }
    
    useEffect(()=>{
        getGroupIntervalSeconds(props.token).then((res)=>{
            setDuration(res);
            var sec=res;
            var day=parseInt(sec/(24*3600));
            sec=parseInt(sec%(24*3600));
            var hour=parseInt(sec/(3600));
            sec=parseInt(sec%(3600));
            var min=parseInt(sec/(60));
            sec=parseInt(sec%(60));
            var t="";
            if(day!=0) t+=day+"d ";
            if(hour!=0)t+=hour+"h ";
            if(min!=0)t+=min+"m ";
            if(sec!=0)t+=sec+"s ";
            setDurationText(t+"prediction")
        })
if(props.asset.toUpperCase()=="BNB")
{
    setCurrentIcon(icon1)
    setfirst("Binance Coin");
} 
    else 
    if(props.asset.toUpperCase()=="BTC")
    {
    setCurrentIcon(icon2);
    setfirst("Bitcoin")
    }
    else if(props.asset.toUpperCase()=="ETH")
    {
    setCurrentIcon(icon3);
    setfirst("Ethereum")
    }
    else if(props.asset.toUpperCase()=="APPLE"||props.asset.toUpperCase()=="APL")
    {
    setCurrentIcon(icon4)
    setfirst("Apple Coin")
    }
    else if(props.asset.toUpperCase()=="GOOGLE"||props.asset.toUpperCase()=="GOOGL")
    {
    setCurrentIcon(icon5);
    setfirst("Google Coin")
    }
    else if(props.asset.toUpperCase()=="TESLA"||props.asset.toUpperCase()=="TSL"||props.asset.toUpperCase()=="TSLA")
    {
    setCurrentIcon(icon6);
    setfirst("Tesla Coin")
    }
    else if(props.asset.toUpperCase()=="CAKE")
    {
    setCurrentIcon(icon7);
    setfirst("PancakeSwap");
    }
    else
    {
    setCurrentIcon(icon8)
    setfirst("Coin")
    }
    },[])
    

    return(
        <tr className={styles.tr}>
        <td className={styles.td1}>
<img src={currentIcon} className={styles.icon}></img>
        </td>
        <td className={styles.td2}>
            <div className={styles.first}>{first}</div>
            <div className={styles.last}>{props.asset}</div>

        </td>
        <td className={styles.td3}>
            <div className={styles.duration}>{durationText}</div>

        </td>
        <td className={styles.td4}>
<button className={classnames(styles.tableButton,buttonColor)} onClick={()=>{
     dispatch({type:"groupChange", currentGroup:props.index});
}}>{buttonText}</button>
        </td>
    </tr>
    );
}
export default Transaction3;